// =============================================================================
// ENDPOINT SUMMARY: GET /v1/endpoints/:endpointId/summary
// =============================================================================
// Returns basic usage + revenue summary for an endpoint this month

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { getDb } from '@/db/client';
import { getEndpointBillingSummary } from '@/lib/billing';

export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const { endpointId } = await params;

  // Authenticate
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  // Verify endpoint exists and belongs to account
  const db = getDb();
  const endpointResult = await db.query<{
    id: string;
    name: string;
    account_id: string;
    price_per_call: string;
    platform_fee_bps: number;
  }>(`
    SELECT id, name, account_id, price_per_call, platform_fee_bps
    FROM endpoints
    WHERE id = $1
  `, [endpointId]);

  if (endpointResult.rows.length === 0) {
    return NextResponse.json(
      { error: 'Endpoint not found', code: 'NOT_FOUND' },
      { status: 404 }
    );
  }

  const endpoint = endpointResult.rows[0];

  if (endpoint.account_id !== accountId) {
    return NextResponse.json(
      { error: 'Endpoint not found', code: 'NOT_FOUND' },
      { status: 404 }
    );
  }

  // Get billing summary
  const summary = await getEndpointBillingSummary(endpointId, accountId);

  return NextResponse.json({
    success: true,
    endpoint: {
      id: endpoint.id,
      name: endpoint.name,
      pricePerCall: endpoint.price_per_call || '0.001',
      platformFeeBps: endpoint.platform_fee_bps || 200,
    },
    thisMonth: {
      calls: summary.calls,
      gross: summary.gross,
      platformCut: summary.platformCut,
      creatorNet: summary.creatorNet,
    },
  });
}

